.jumbotron {
  height: 100vh;
}

.landing-page-container {
  padding-top: 90px;
  margin: 0 48px 0 48px;
}

.main-background {
  position: absolute;
  height: 100%;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -1;
}

.landing-page-title {
  max-width: 627px;
  margin: 0 0 38px 0;
  font-size: 49px;
  font-weight: 300;
}

.landing-page-caption {
  font-size: 17.5px !important;
  font-weight: 300;
}

/* News styles */

/* Large Styles width greater than 1024 pixels */

.news-lg {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  position: absolute;
  max-width: 100%;
  bottom: 0;
}

.news-title-lg {
  width: 339px;
  height: 43px;
  background: rgba(223, 133, 67, 0.7);
  border-radius: 9px 9px 0 0;
  margin: 0 0 0 48px;
}

.news-cards-lg {
  display: flex;
  max-width: 100vw;
  padding: 0px 48px 0 48px;
  /* overflow: hidden; */
}

.news-title-lg > div {
  font-weight: 300;
  margin: 0px 10px 0px 10px;
  text-align: center;
  font-size: 17.5px;
  transform: translateY(50%);
}

/* Small Styles width lower or equal to 1024 pixels */

.news-sm {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  position: relative;
  width: 100%;
}

.news-title-sm {
  display: flex;
  height: 53px;
  width: 100%;
  background: rgba(223, 133, 67, 1);
  align-items: center !important;
  justify-content: center;
  padding: 0px 24px 0px 24px;
}

.news-title-sm > div {
  font-weight: 100;
  font-size: 20px;
}

@media screen and (max-width: 1024px) {
  .jumbotron {
    height: calc(100vh - 53px);
  }

  .main-background {
    height: calc(100vh - 53px);
  }

  .landing-page-container {
    margin: 0 24px 0 24px;
  }
}

.news-card {
  background-color: rgba(255, 255, 255, 0.1);
  padding: 30px;
  width: 426px;
  max-height: 208.26px;
  /* backdrop-filter: blur(2px);
  -webkit-backdrop-filter: blur(2px); */
  display: flex;
  transition: ease all 0.3s;
  position: relative;
}

.news-card:hover {
  transform: scale(1.1);
  height: 468;
  border-top-left-radius: 19px;
  border-top-right-radius: 19px;
  background-color: rgba(255, 255, 255, 0.481);
}

.news-card:hover > .news-notch {
  display: block;
  opacity: 1;
  background-color: rgb(255, 255, 255);
}

.news-notch {
  opacity: 0;
  position: absolute;
  background-color: rgb(255, 255, 255);
  width: 25%;
  height: 10px;
  top: -6px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 8px;
  transition: ease all 0.3s;
  /* z-index: 1000; */
}

.news-card:hover > .info-caption > a {
  color: black;
  transition: ease all 1s;
}

.news-card-right {
  border-top-right-radius: 19px;
}

.thumbnail-container {
  width: 172px;
  height: 130px;
}

.thumbnail-container > img {
  max-width: 100%;
  max-height: 100%;
  display: block;
}

.info-caption {
  padding: 0px 0px 0px 20px;
  width: 54.24% /*142.64px*/;
  height: 130px;
}

.skeleton-bars {
  max-width: 100%;
}

.info-caption > a {
  color: #ffffff;
  font-size: 14px;
  font-weight: 300;
  text-decoration: none;
}

@media screen and (max-width: 1104px) {
  .info-caption > a {
    font-size: 0.9rem;
  }

  .thumbnail-container {
    width: 53.82%;
  }
}

@media screen and (max-width: 1024px) {
  .news-cards {
    display: none;
  }
}

/* Small news section */

.news-card-sm {
  height: 198px;
  width: 100%;
  overflow: hidden;
  position: relative;
}

.background-thumbnail-sm {
  width: 100%;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: all 0.3s ease-in-out;
}

.info-caption-sm {
  background-color: rgba(0, 0, 0, 0.6);
  text-align: left;
  position: absolute;
  width: 100%;
  height: 100%;
  bottom: 0;
  padding: 24px;
  overflow: hidden;
  transition: all 0.3s ease-in-out;
}

.info-caption-sm > a {
  color: #ffffff;
  font-size: 17.5px;
  font-weight: 100;
  text-decoration: none;
}

