  line-height: 1.3;
}

.news-card-sm:hover > .background-thumbnail-sm {
  transform: scale(1.05);
}

.news-card-sm:hover > .info-caption-sm {
  background-color: rgba(0, 0, 0, 0.8);
}

.placeholder-item {
  box-shadow: 0 4px 10px 0 rgba(33, 33, 33, 0.15);
  border-radius: 4px;
  background: #d5d5d5;
  width: 100%;
  position: relative;
  overflow: hidden;
}

/* The ::before will be placed absolute to its parent */
/* overflow: hidden; will prevent the gradient from showing outside our placeholder element */

.placeholder-item::before {
  content: "";
  display: block;
  position: absolute;
  left: -150px;
  top: 0;
  height: 100%;
  width: 100px;
  background: linear-gradient(
    to right,
    transparent 0%,
    #e6e6e6 50%,
    transparent 100%
  );
  -webkit-animation: load 1.4s cubic-bezier(0.4, 0, 0.2, 1);
          animation: load 1.4s cubic-bezier(0.4, 0, 0.2, 1);
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes load {
  from {
    left: -150px;
  }
  to {
    left: 100%;
  }
}

@keyframes load {
  from {
    left: -150px;
  }
  to {
    left: 100%;
  }
}

